<template>
  <div>
    <div v-for="(item,index) in paperDetailVO" :key="index">
      <el-row class="issue" v-if="item.issueTypeID === 1">
        <!-- 单选题 -->
        <el-col>
          <div>
            <span>第{{item.issueNum}}题：{{item.issueContent}}</span>
          </div>
          <div>
            <el-radio-group>
              <el-radio label="A">A.{{item.optionA}}</el-radio>
              <el-radio label="B">B.{{item.optionB}}</el-radio>
              <el-radio label="C">C.{{item.optionC}}</el-radio>
              <el-radio label="D">D.{{item.optionD}}</el-radio>
            </el-radio-group>
          </div>
        </el-col>
      </el-row>
      <el-row class="issue" v-else-if="item.issueTypeID === 2">
        <!-- 多选题 -->
        <el-col>
          <div>
            <span>第{{item.issueNum}}题：{{item.issueContent}}</span>
          </div>
          <div>
            <el-checkbox-group v-model="checkboxPick[item.issueNum]">
              <el-checkbox label="A">A.{{item.optionA}}</el-checkbox>
              <el-checkbox label="B">B.{{item.optionB}}</el-checkbox>
              <el-checkbox label="C">C.{{item.optionC}}</el-checkbox>
              <el-checkbox label="D">D.{{item.optionD}}</el-checkbox>
            </el-checkbox-group>
          </div>
        </el-col>
      </el-row>
      <el-row class="issue" v-else>
        <!-- 判断题 -->
        <el-col>
          <div>
            <span>第{{item.issueNum}}题：{{item.issueContent}}</span>
          </div>
          <div>
            <el-radio-group>
              <el-radio label="Y">正确</el-radio>
              <el-radio label="N">错误</el-radio>
            </el-radio-group>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      paperDetailVO: [],
      checkboxPick: [[],[],[],[],[]]
    };
  },
  methods: {
    getData() {
      var api =
        "http://localhost:8888/paper/detail/" + this.$route.params.paperID;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.paperDetailVO = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
.issue {
  height: 100px;
  border-radius: 2px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  font: 16px Medium;
  margin-bottom: 5px;
}
</style>